.content {
  max-width: 10rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.content .content-banner {
  position: relative;
  width: 100%;
}

.content .content-banner .btn {
  position: absolute;
  left: 30%;
  bottom: 60px;
  display: block;
  width: 39%;
  height: 60px;
}

.content .rule {
  width: 100%;
  background-color: #f4d4dd;
  color: #e03155;
}

.content .rule dl {
  width: 90%;
  margin: 0 auto;
}

.content .rule dl dt {
  font-weight: bold;
  text-align: center;
  font-size: 14px;
  padding: 0.533333rem 0;
}

.content .rule dl dd {
  font-size: 12px;
  text-align: justify;
  margin: 0.133333rem 0;
}

.content .rule dl dd:last-child {
  padding-bottom: 0.533333rem;
}
